.about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: px_to_vw_desktop(250);
    p {
        @include mulish500();
        font-size: 16px;
        line-height: 1.5em;
        letter-spacing: 0.02em;
        color: #3E3025;
        text-align: center;
        max-width: 788px;
        @include windowMax1700p{
            font-size: 14px;
            max-width: 650px;
        }
        @include windowMax1280p{
            font-size: 12px;
            max-width: 540px;
        }
    }

    span {
        line-height: 1.75em;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #B58C67;
        padding-top: 0px;
    }

    h1 {
        @include tenor();
        font-size: px_to_vw_desktop(48);
        line-height: 1.4166666em;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        text-align: center;
        color: #B58C67;
        max-width: 23.9583333em;
        padding-top: 0.83333em;
        padding-bottom: 32px;
    }
}